import Cookie from "js-cookie";
export default {
  state: {
    isCollapse: false,// 控制菜单收缩;
    tabsList: [{
      path: '/',
      name: 'home',
      label: '首页',
      icon: 's-home',
      url: 'Home/Home'
    }

    ],//面包学的数据
    menu:[],
  },
  mutations: {
    collapseMenu(state) {
      state.isCollapse = !state.isCollapse;
    },
    //更新面包学
    
    //这是mu的数据
    setMen(state,val){
      state.menu=val
      Cookie.set('menu',JSON.stringify(val))
    }
  }

}