<template>
  <div class="manage">
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="50%"
      :before-close="handleClose"
    >
      <el-form
        ref="form"
        :rules="rules"
        :inline="true"
        :model="form"
        label-width="80px"
      >
        <el-form-item label="姓名" prop="name">
          <el-input placeholder="这是背景样式" v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input placeholder="这是背景样式" v-model="form.age"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select v-model="form.sex" placeholder="请选择活动区域">
            <el-option label="武装直升机" :value="0"></el-option>
            <el-option label="椰子" :value="1"></el-option>
            <el-option label="未知" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="选择日期" prop="birth">
          <el-date-picker
            v-model="form.birth"
            type="date"
            placeholder="选择日期"
            value-format="yyyy-MM-DD"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="地址" prop="addr">
          <el-input placeholder="这是背景样式" v-model="form.addr"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
    <div class="mangeheader">
      <el-button type="primary" @click="handleadd">+添加</el-button>
      <!--form-->
    </div>
    <el-table height="90%" :data="tableData" style="width: 100%">
      <el-table-column prop="name" label="姓名"> </el-table-column>
      <el-table-column prop="sex" label="性别">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{
            scope.row.sex == 1 ? "男" : "女"
          }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="age" label="年龄"> </el-table-column>
      <el-table-column prop="birth" label="出生日期"> </el-table-column>
      <el-table-column prop="addr" label="地址"> </el-table-column>
      <el-table-column prop="addr" label="操作">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleED(scope.row)">编辑</el-button>
          <el-button size="mini" @click="handlede(scope.row)" type="danger"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div>
      <!--分页器-->
    <el-pagination
              layout="prev, pager, next"
              :total="total"
              @current-change="handlepage">
  </el-pagination>
    </div>
     
  </div>
</template>

<script>
import { addUser, getUser, editUser, delUser } from "../api";
export default {
  name,
  data() {
    return {
      dialogVisible: false,
      form: {
        name: "",
        age: "",
        sex: "",
        birth: "",
        addr: "",
      },
      rules: {
        name: [{ required: true, message: "请输入活动名称" }],
        age: [{ required: true, message: "请输入活动名称" }],
        sex: [{ required: true, message: "请输入活动名称" }],
        birth: [{ required: true, message: "请输入活动名称" }],
        addr: [{ required: true, message: "请输入活动名称" }],
      },
      tableData: [],
      moda1type: 0,
      total:0,
      pageData:{
        page:1,
        limit:10,

      }
    };
  },
  methods: {
    submit() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.moda1type === 0) {
            addUser(this.form).then(() => {
              this.getList();
            });
          } else {
            editUser(this.form).then(() => {
              this.getList();
            });
          }
          this.dialogVisible = false;
        }
      });
    },
    //弹窗关闭的方法
    handleClose() {
      this.$refs.form.resetFields();
      this.dialogVisible = false;
    },
    cancel() {
      this.handleClose();
    },
    //编辑
    handleED(row) {
      (this.moda1type = 1),
        (this.dialogVisible = true),
        (this.form = JSON.parse(JSON.stringify(row)));
    },
    //删除
    handlede(row) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          delUser({ id: row.id }).then(() => {
            this.$message({
              type: "success",
              message: "删除成功!",
            });
            this.getList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    //获取列表数据
    getList() {
      getUser({params:this.pageData}).then(({ data }) => {
        this.tableData = data.list;
        this.total = data.count || 0 
      });
    },
    handleadd() {
      this.moda1type = 0;
      this.dialogVisible = true;
    },
    //当我们选择页码的时候的回调
    handlepage(val){
      this.pageData.page= val,
      this.getList();
    }
  },
  mounted() {
    this.getList();
  },
};
</script>

<style>
.manage {
  height: 90%;
}
</style>